
.order {
    width: 90%;
    margin: 0 auto;
    .order_hd {
        height: 3.5rem;
        i {
            font-size: 0.5rem;
            line-height: 1.5rem;
        }
        h2 {
            font-size: 0.64rem;
            line-height: 1.5rem;
        }
    }
    .am-tabs-default-bar-top {
        .am-tabs-default-bar-content {
            width: 65%;
        }
        .am-tabs-default-bar-underline {
            width: 20% !important;
            margin: 0px 6%;
            border-color: #008489;
        }
    }
    .am-tabs-default-bar-top .am-tabs-default-bar-tab::after{
        background: none !important;
    }
    .noorder{
        h2 {
            font-size: 0.32rem;
            font-weight: bold;
            color: #666;
            line-height: 0.6rem;
            border-top: 2px solid #ccc;
            padding-top: 0.5rem;
        }
        p {
            font-size: 0.32rem;
            font-weight: bold;
            width: 50%;
            line-height: 0.8rem;
            margin-top: 0.2rem;
            color: #008489;
            text-align: center;
            border: 2px solid #008489;
        }
    }
    .orderbox {
        padding: 0 0.1rem;
        li {
            width: 100%;
            height: 2.5rem;
            padding: 0.5rem 0;
            box-sizing: border-box;
            border-bottom: 1px solid #eee;
            display: flex;
            .ordertxt {
                width: 70%;
                line-height: 0.5rem;
                h2 {
                    width: 90%;
                    font-size: 0.3rem;
                    font-weight: bold;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
                p {
                    font-size: 0.28rem;
                    span {
                        margin-right: 0.2rem;
                    }
                }
            }
            .orderimg {
                width: 30%;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}